.tamarind {
  position: relative;
  width: 1000px;
  height: 400px;
  border-radius: 5px;
  background-color: #555555;
  color: #eeeeee;
  font-family: sans-serif;
}
.tamarind-unsupported {
  height: auto;
  padding: 20px;
}
.tamarind-unsupported-icon {
  margin-right: 20px;
  font-size: 30px;
}
.tamarind-logo {
  position: absolute;
  bottom: 10px;
  left: 50px;
  transform: rotate(-90deg);
  transform-origin: left bottom 0;
  font-family: "tamarind-logo";
  font-size: 30px;
  line-height: 50px;
  color: #505050;
  text-shadow: -1px 1px 1px rgba(255, 255, 255, 0.075), 1px -1px 1px rgba(0, 0, 0, 0.25);
}
.tamarind-logo:before {
  content: "Tamarind";
}
.tamarind-menu {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 50px;
}
.tamarind-menu-button {
  color: #eeeeee;
  text-decoration: none;
  margin: 5px 0 0 5px;
  display: block;
  width: 55px;
  height: 50px;
  font-size: 25px;
  text-align: center;
  line-height: 55px;
  border-radius: 5px;
  padding-right: 10px;
  box-sizing: border-box;
}
.tamarind-menu-button:hover {
  color: #FFF;
  text-shadow: 0 0 10px #FFF;
}
.tamarind-menu-button.is-selected {
  background-color: #eeeeee;
  color: #555555;
  text-shadow: none;
  cursor: default;
}
.tamarind-editor-panel {
  position: absolute;
  top: 5px;
  left: 50px;
  bottom: 5px;
  width: 400px;
  border-radius: 5px;
  background-color: #eeeeee;
  color: #555555;
}
.tamarind-editor-code {
  height: 100%;
  padding: 5px;
  box-sizing: border-box;
  position: relative;
}
.tamarind-editor-code .CodeMirror {
  height: 100%;
  background: #eeeeee;
}
.tamarind-editor-code .CodeMirror-gutters {
  background: #eeeeee;
}
.tamarind-editor-config {
  padding: 10px;
}
.tamarind-number-input {
  width: 40px;
}
.tamarind-program-error {
  border: solid 1px #EEA;
  background: #FFE;
  padding: 5px;
  margin-left: 48px;
}
.tamarind-program-error .CodeMirror-lint-marker-error {
  cursor: default;
}
.tamarind-render-panel {
  position: absolute;
  top: 5px;
  left: 455px;
  right: 5px;
  bottom: 5px;
  border-radius: 5px;
  background: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQCAMAAAAoLQ9TAAAABlBMVEXW1tb///9kNAxTAAAAFUlEQVQYlWNggAJGKGAYIIGBth8KAEUQAIEP5jj0AAAAAElFTkSuQmCC");
  overflow: hidden;
  -webkit-mask-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAIAAACQd1PeAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAA5JREFUeNpiYGBgAAgwAAAEAAGbA+oJAAAAAElFTkSuQmCC");
  /* this fixes the overflow:hidden in Chrome/Opera */
}
.tamarind-inset,
.tamarind-menu-button.is-selected,
.tamarind-editor-panel,
.tamarind-render-canvas {
  box-shadow: 0.5px 0.5px 1px 0 rgba(255, 255, 255, 0.25);
}
.tamarind-inset-text,
.tamarind-menu-button {
  text-shadow: 1px 1px 1px rgba(255, 255, 255, 0.25);
}
.tamarind-emboss,
.tamarind {
  box-shadow: inset 1px 1px 1px 0 rgba(255, 255, 255, 0.5), inset -1px -1px 1px rgba(0, 0, 0, 0.25);
}
.tamarind-emboss-text,
.tamarind-menu-button.is-selected {
  text-shadow: -1px -1px 1px #ffffff, 1px 1px 1px rgba(0, 0, 0, 0.25);
}
.tamarind-render-canvas {
  width: 100%;
  height: 100%;
  border-radius: 5px;
}
